$gutter: 5px;

.container {
    position: relative;
    width: 100%;
    border: 3px solid tomato;
    white-space: nowrap;
    font-size: 0;
    padding: 0;
    overflow: scroll;
    -webkit-overflow-scrolling: touch;
}

.inner {
    position: relative;
    padding: $gutter;
}

.header {
    position: sticky;
    top: 0;
    left: 0;
    padding: $gutter;
    display: inline-block;
    font-size: 30px;
    width: 100%;
    background: tomato;
    color: white;
    position: absolute;
    top: 0;
    left: 0;

    &-push {
        position: relative;
        z-index: 1;
    }
}

.column {
    display: inline-block;
    vertical-align: top;
    width: 300px;
    margin-right: $gutter;
}

.row {
    background: #222;

    &::after {
        content: "";
        display: block;
        width: 100%;
        height: 0;
        padding-bottom: 25%;
    }

    & + & {
        margin-top: $gutter;
    }
}
